/**
 * MAIN
 *
 * Import all style files.
 * 
 * Syntaxis is: @import "route/fileName"
 */
/* Generic
   ========================================================================== */
/**
 * THEME
 *
 * Theme configuration.
 */
/* Text
   ========================================================================== */
/* Colors
   ========================================================================== */
/**
 * BASE
 *
 * File used to add common styles across all page.
 */
/* Body
   ========================================================================== */
body {
  font-family: "Roboto", sans-serif;
  font-size: 16px; }

/* Paragraphs
   ========================================================================== */
p {
  margin: 10px 15px; }

/* Anchors
   ========================================================================== */
a {
  border-bottom: 2px solid #1c1c1c;
  color: #1c1c1c;
  text-decoration: none; }
  a:hover {
    border-bottom-color: #666;
    color: #666; }

/* Utils
   ========================================================================== */
/**
 * TEXT ALIGN
 */
.u-alignRight {
  text-align: right; }

/**
 * BORDER-RADIUS
 */
/* Reset
   ========================================================================== */
/* Values
   ========================================================================== */
/**
 * FLEXBOX
 */
.u-flex {
  display: flex; }

/**
 * MARGINS
 */
.u-marginTop50 {
  margin-top: 50px; }

.u-marginBottom50 {
  margin-bottom: 50px; }

/**
 * MEASUREMENT
 */
/* Width
   ========================================================================== */
.u-halfWidth {
  width: 50%; }

/* Height
   ========================================================================== */
.u-fullHeight {
  min-height: calc(100vh - 383px); }

/**
 * WRAPPER
 */
.u-wrapper {
  height: 100vh; }

/* Content
   ========================================================================== */
.u-wrapperContent {
  max-width: 1024px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px; }

/* Components
   ========================================================================== */
/**
 * ACCORDION
 */
/* Title
   ========================================================================== */
.Accordion-title {
  padding: 15px;
  text-transform: uppercase;
  cursor: pointer; }
  .Accordion-title:hover {
    opacity: 0.85; }

.Accordion-title:first-child {
  border-radius: 4px 4px 0 0; }

.Accordion-title:last-of-type:not(.is-active) {
  border-radius: 0 0 4px 4px; }

/* Title modifiers */
.Accordion-title--blue {
  background-color: #2bdfff;
  color: #1c1c1c; }

.Accordion-title--yellow {
  background-color: #f9ce4a;
  color: #1c1c1c; }

.Accordion-title--red {
  background-color: #FA8072;
  color: #fff; }

.Accordion-title--gray {
  background-color: #a5a5a5;
  color: #1c1c1c; }

.Accordion-title--green {
  background-color: #98FB98;
  color: #1c1c1c; }

/* Description
   ========================================================================== */
.Accordion-description {
  box-sizing: border-box;
  max-height: 0;
  margin-left: 0;
  background-color: #f6f6f6;
  line-height: 1.7;
  overflow: hidden;
  transition: all 0.3s ease; }

.Accordion-description:last-child {
  border-radius: 0 0 4px 4px; }

/**
 * HEADER
 */
.Header {
  padding: 50px 0;
  background-color: #40efb8;
  color: #1c1c1c; }

/* H1
   ========================================================================== */
.Header-mainText {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 20px; }

/* H2
   ========================================================================== */
.Header-secondaryText {
  margin: 0;
  line-height: 1.3;
  font-weight: 300;
  font-size: 28px; }

/**
 * FOOTER
 */
.Footer {
  padding: 30px 0;
  background-color: #1c1c1c;
  color: #d4d4d4;
  font-weight: 300; }

/* Footer heart
   ========================================================================== */
.Footer-heart {
  color: #ff1c51; }
